
.component-container{

    width: 100%;
    height:100%;
    position: relative;
}
.headers{
    text-align: center;
    font-size: 4vw;
    width: 100%;
    background-color: rgb(214, 214, 214);
}
.banner{
    position:relative;
    display: block;
    width: 100%;
    height: 90vh;
    z-index: -1;
   .banner-text{
        display:block ;
        position: absolute;
        text-align: center;
        width: 100%;
        height: 100%;
        font-family: 'Lato', sans-serif;
        z-index: 3;
        animation: fadein 3s ease-in forwards;
            h1{
                font-size: 7vw; 
                margin: 10% 20% 2% 0%; 
                animation:fadein 1s ease-in 0.5s forwards;
                   
            }
            h2{
                font-size: 5vw;
                margin: 2% 0% 2% 0%; 
                animation:fadein 1s ease-in 0.7s forwards;
            }
            h3{
                font-size: 3vw;
                margin: 2% 0% 2% 30%; 
                animation:fadein 1s ease-in 1s forwards;
            }
    }
   
}
 

@keyframes fadein{
   0%{
    opacity: 0;
   }
   50%{
    opacity: 1;
    color: #181818;
    
   }
   100%{
    text-shadow: 1px 1px 10px white;
    color: white;
    
   }
}

@media(max-width:600px){
    .menu{
        display: none;
    }
    .banner{
        height: 25vh;
    }
    .pw-container{
        display: none;
    }
    .contact-container{
        height:25vh
    }
    .form-container{
        max-height: 25vh;
        textarea{
            height: 50%;
        }
    }
    .rightbox-container{
        display: none;
    }
}